<template>
  <div>
    <Header name="订单详情"></Header>
    <div style="background: #f7f7f7" >
      <div v-for="item in dataList" :key="item.id">
      <div class="header-text">
        <img
          :src="require('@/assets/image/background.png')"
          alt=""
          width="100%"
          height="82px"
        />
        <div class="orderMsg">
          <div class="orderfont">
            <h1 v-if="returnApply !== 0 && returnApply.status < 2">退款中</h1>
            <h1 v-if="item.status === 10">退货中</h1>
            <h1 v-if="item.status === 12">退货已完成</h1>
            <h1 v-if="returnApply !== 0 && returnApply.status === 2">退款已完成</h1>
            <h1 v-if="returnApply === 0">{{item | fontJudge}}</h1>
            <h1 v-if="item.status === 6">未付款待发货</h1>
            <h1 v-if="item.status === 7">未付款已发货</h1>
            <h1 v-if="item.status === 8">未付款已收货</h1>
            <p v-if="item.status === 0 &&item.payType === 0">请在24小时内完成支付</p>
            <p v-if="item.status === 0 && item.payType === 3">等待商家审核</p>
            <p v-if="item.status === 2">{{item.deliveryTime | automaticReceipt(item.autoConfirmDay)}}后自动确认收货</p>
            <p v-if="returnApply !== 0 && returnApply.status < 2">预计1-3个工作日原路返回到您账户</p>
            <p v-if="returnApply !== 0 && returnApply.status === 2">退款时间：{{returnApply.receiveTime}}</p>
          </div>
          <img :src="require('@/assets/image/shop.png')" alt="" v-if="item.status === 1 || item.status === 2 || item.status === 6 || item.status === 7" width="64px" height="62px"/>
          <img :src="require('@/assets/image/shopbasket.png')" alt="" v-if="item.status === 0" width="56px" height="56px"/>
          <img :src="require('@/assets/image/cardboard.png')" alt="" v-if="item.status === 3 || item.status === 4 || item.status === 8 || item.status === 10 || item.status === 12" width="67px" height="54px"/>
        </div>
      </div>
      <div class="address">
        <div class="center">
          <div class="img">
            <img
              :src="require('@/assets/image/weizhi.png')"
              alt=""
              width="44px"
              height="44px"
            />
          </div>
          <div class="dizhi">
            <p>{{item.receiverName}}&emsp;<span>{{item.receiverPhone}}</span></p>
            <p>{{item.receiverProvince}}&nbsp;{{item.receiverCity}}&nbsp;{{item.receiverRegion}}&nbsp;{{item.receiverTown}}{{item.receiverDetailAddress}}</p>
          </div>
        </div>
      </div>
      <div class="buyMsg">
        <div class="ordernum">
          <p>订单号：<span>{{item.orderSn}}</span></p>
          <p>{{item | statusFilter}}</p>
        </div>
        <div class="topMsg" v-for="itm in item.orderItemList" :key="itm.id">
          <div class="waresMsg">
            <div class="waresImage">
              <van-image :src="itm.productPic ? itm.productPic:''"></van-image>
            </div>
            <div class="waresWord">
              <div class="ttop">
                {{itm.productName}}
              </div>
              <div class="shopSpecs" v-if="itm.productAttr">
                <div v-for="(ite,i) in JSON.parse(itm.productAttr || `[]`)" :key="i">{{ite.value}}</div>
              </div>
              <div class="ccenter">{{itm.promotionName}}</div>
              <div class="bbottom">
                <div class="amount" v-if="item.payType === 5"><span>{{itm.productPrice}}<span style="font-size:14px;margin:0">积分</span></span>x{{itm.productQuantity}}</div>
                <div class="amount" v-else><span>￥{{itm.productPrice}}</span>x{{itm.productQuantity}}</div>
                <div class="number" v-if="aa(itm.id, item.returnApplyList,itm.orderId)" @click="lookOver(item.orderSn,itm.orderId,item.returnApplyList,itm.id)">查看售后</div>
                <div class="number" v-else @click="applyReturn(item.id,1,itm.id,itm.allowReturn, item.orderItemList, item.status)" v-show="(item.status === 1 || item.status === 3 || item.status === 8) && item.payType !== 5">申请售后</div>
              </div>
            </div>
          </div>
        </div>
        <div class="centerMsg">
          <div class="content01" v-if="item.payType != 5">
            <p>商品总金额</p>
            <p>￥{{item.totalAmount || 0}}</p>
          </div>
          <div class="content01" v-else>
            <p>商品总金额</p>
            <p>{{item.totalAmount || 0}}<span style="font-size: 12px;">积分</span></p>
          </div>
          <div class="content01">
            <p>运费</p>
            <p>￥{{item.freightAmount || 0}}</p>
          </div>
          <div class="content01">
            <p>会员折扣</p>
            <p style="color: #f44;">-￥{{item.promotionAmount || 0}}</p>
          </div>
          <div class="content01">
            <p>商家优惠</p>
            <p style="color: #f44;">-￥{{item.discountAmount ? item.discountAmount : 0}}</p>
          </div>
          <div class="content01">
            <p>
              代金券抵扣
            </p>
            <p style="text-align: right;color: #f44;">{{!item.voucherAmount ? '-￥0' : '-￥' + item.voucherAmount}}</p>
          </div>
        </div>
        <div class="bottomMsg">
          <div class="shopDis">
            <p>已优惠&nbsp;<span>{{(item.discountAmount ? item.discountAmount + (item.voucherAmount || 0) + item.promotionAmount : (item.voucherAmount || 0) + item.promotionAmount).toFixed(2)}}</span>&nbsp;元&emsp;共&nbsp;{{item.orderItemList.length}}&nbsp;件&emsp;<span
              >¥{{item.payAmount || 0}}</span></p>
            <p class="jifen">可获得积分：<span>{{(item.integration > 0 ? item.integration : 0) || 0}}</span></p>
          </div>
        </div>
        <div style="height: 19px"></div>
      </div>
      <div class="orderdetails">
        <div class="title">订单信息</div>
        <div class="bbody">
          <p>订单编号：<span>{{item.orderSn}}</span></p>
          <p>创建时间：<span>{{item.createTime}}</span></p>
          <p>付款时间：<span>{{item.paymentTime}}</span></p>
          <p>订单备注：<span>{{item.note || '-----'}}</span></p>
        </div>
      </div>
      <div class="refundmsg" v-if="returnApply !== 0">
        <div class="title">退款信息</div>
        <div class="bbody">
          <p>退款原因：<span>{{returnApply.reason}}</span></p>
          <p>退款金额：<span>￥{{returnApply.returnAmount || 0}}</span></p>
          <p>申请时间：<span>{{returnApply.createTime}}</span></p>
          <p v-if="returnApply !== 0 && returnApply.status === 2">完成时间：<span>{{returnApply.receiveTime}}</span></p>
          <p>退款编号：<span>{{returnApply.orderSn}}</span></p>
        </div>
      </div>
      <div class="orderdetails" v-if="item.status === 4">
        <div class="title">已取消</div>
        <div class="bbody">
          <p>取消原因：<span>{{item.cancelReason}}</span></p>
          <p>创建时间：<span>{{item.createTime}}</span></p>
          <p>取消时间：<span>{{item.modifyTime}}</span></p>
        </div>
      </div>
      <div class="btn">
        <van-button
          plain
          color="#b3b3b3"
          size="small"
          :style="{ borderRadius: '3px' ,marginRight:'15px'}"
          @click="showPopup(item.id)"
          v-if="item.status === 0 || item.status === 6"
          :disabled="isCancel"
          >取消订单
        </van-button>
        <van-button
          size="small"
          color="#FD2744"
          :style="{ borderRadius: '3px',marginRight:'15px'}"
          @click="nowPayment(item)"
          v-if="item.status === 0  || item.status === 6 || item.status === 7 || item.status === 8"
          :disabled="confirm"
          >{{confirm ? '审核中' : '立即付款'}}</van-button
        >
        <van-button
          plain
          color="#b3b3b3"
          size="small"
          :style="{ borderRadius: '3px', marginRight:'15px' }"
          @click="toLogistics(
            {com:item.deliveryCompany,
            num:item.deliverySn,
            phone:item.receiverPhone,
            }
          )"
          v-if="item.status === 2"
          >查看物流</van-button
        >
        <!-- <van-button
          plain
          color="#b3b3b3"
          size="small"
          :style="{ borderRadius: '3px', marginRight:'15px' }"
          @click="toInvoice"
          v-if="item.status === 2 && returnApply"
          >开发票</van-button
        > -->
        <van-button
          size="small"
          color="#FD2744"
          :style="{ borderRadius: '3px', marginRight:'15px' }"
          @click="sureRece(item.id)"
          v-if="item.status === 2"
          >确认收货</van-button
        >
        <!-- <van-button
          plain
          color="#b3b3b3"
          size="small"
          :style="{ borderRadius: '3px' ,marginRight:'15px'}"
          @click="applyReturn(item.id,1)"
          v-if="item.status === 3 && !returnApply"
          >申请退货
        </van-button> -->
        <van-button
          size="small"
          color="#FD2744"
          :style="{ borderRadius: '3px', marginRight:'15px' }"
          @click="toInvoice(item)"
          v-if="item.payType !== 5 && getInvoiceFlag(item) && !item.invoiceId && item.status === 3"
          >申请开票</van-button
        >
        <!-- 
          v-if="item.status === 3 && !returnApply && !item.invoiceId"
          :disabled="item.payType === 5" -->
        <van-button
          size="small"
          color="#BEBEBE"
          :style="{ borderRadius: '3px', marginRight:'15px' }"
          v-if="item.status === 3 && !returnApply && item.invoiceId"
          @click="seeInvoice(item.invoiceId)"
          >已申请开票</van-button
        >
        <van-button
          plain
          color="#b3b3b3"
          size="small"
          :style="{ borderRadius: '3px' ,marginRight:'15px'}"
          v-if="item.status === 4"
          @click="delOrder(item.id)"
          >删除订单
        </van-button>
      </div>
      <van-popup
          v-model="show"
          round
          position="bottom"
          :style="{ height: '52%' }"
        >
          <van-picker
            title="选择理由"
            show-toolbar
            toolbar-position="top"
            :columns="columns"
            @confirm="onConfirm"
            @cancel="onCancel"
            item-height="50px"
          />
      </van-popup>
      <van-action-sheet v-if="showInvoice" v-model="showInvoice" close-on-click-action style="background: #FFFFFF; min-height: 600px;" title="发票信息" @cancel="onCancelInvoice">
        <van-tabs v-model="active" class="invoice-context-info">
          <van-tab title="电子普通发票" name="a">
            <div class="invoiceBody">
              <van-radio-group v-model="formfm.type" direction="horizontal" style="margin-bottom: 15px;">
                <van-radio name="1" @click="clickRadio('1')">个人
                  <template #icon="props">
                    <img class="img-icon" :src="props.checked ? require('@/assets/image/yxz.png') : require('@/assets/image/wxz.png')" width="20px" height="20px"/>
                  </template>
                </van-radio>
                <van-radio name="2" @click="clickRadio('2')">单位
                  <template #icon="props">
                    <img class="img-icon" :src="props.checked ? require('@/assets/image/yxz.png') : require('@/assets/image/wxz.png')" width="20px" height="20px"/>
                  </template>
                </van-radio>
              </van-radio-group>
              <div class="personInp" v-if="formfm.type === '1'">
                <van-form @submit="submitBtnFormfm" ref='formfm'>
                  <van-field
                    v-model="formfm.invoiceTitle"
                    name="发票抬头"
                    label="发票抬头"
                    placeholder="请输入发票抬头"
                    :rules="[{ required: true }]"
                  />
                  <van-field
                    v-model="formfm.email"
                    type="email"
                    name="邮箱"
                    label="邮箱"
                    placeholder="请输入正常使用的邮箱"
                  />
                  <van-button class="paymentBtn" round block type="info" native-type="submit">确认</van-button>
                </van-form>
              </div>
              <div class="companyInp" v-if="formfm.type === '2'">
                <van-form @submit="submitBtnFormfm" ref='formfm'>
                  <van-field
                    v-model="formfm.invoiceTitle"
                    name="发票抬头"
                    label="发票抬头"
                    placeholder="请输入发票抬头"
                    :rules="[{ required: true }]"
                  />
                  <van-field
                    v-model="formfm.taxpayerId"
                    type="taxpayerId"
                    name="纳税人识别号"
                    label="纳税人识别号"
                    placeholder="请按税务登记证填写"
                  />
                  <van-field
                    v-model="formfm.email"
                    type="email"
                    name="邮箱"
                    label="邮箱"
                    placeholder="请输入正常使用的邮箱"
                  />
                  <van-button class="paymentBtn" round block type="info" native-type="submit">确认</van-button>
                </van-form>
              </div>
              <div class="tishi">
                <img :src="require('@/assets/image/warn.png')" alt="" width="12px" height="12px"/>
                <p>申请发票24小时后，即可在您的邮箱中下载电子普通发票。</p>
              </div>
            </div>
          </van-tab>
          <van-tab title="专用发票" name="b">
            <div class="purpose">
              <div class="topFont">
                <div class="condition">
                  开具专用发票的需订单金额至少累计<span>10000</span>元起
                </div>
                <div class="operation">
                  <div class="inLeft">当前订单金额<span>{{order.amount}}</span>元</div>
                  <div class="inRight" @click="toOrderList">
                    <span class="toMerge">合并其他订单金额&nbsp;&gt;</span>
                  </div>
                </div>
                <div style="height: 8px"></div>
              </div>
    
              <div class="entryMsg" v-if="invChoice">
                <van-form @submit="nextStep" ref='formin'>
                  <van-field
                    v-model="formin.invoiceTitle"
                    name="单位名称"
                    label="单位名称"
                    placeholder="请输入单位名称"
                    :rules="[{ required: true }]"
                  />
                  <van-field
                    v-model="formin.taxpayerId"
                    name="纳税人识别号"
                    label="纳税人识别号"
                    placeholder="请输入纳税人识别号"
                  />
                  <van-field
                    v-model="formin.companyAddress"
                    name="注册地址"
                    label="注册地址"
                    placeholder="注册地址"
                    :rules="[{ required: true }]"
                  />
                  <van-field
                    v-model="formin.telphone"
                    name="注册电话"
                    label="注册电话"
                    placeholder="注册电话"
                    :rules="[{ required: true }]"
                  />
                  <van-field
                    v-model="formin.bankName"
                    name="开户银行"
                    label="开户银行"
                    placeholder="请输入开户银行"
                    :rules="[{ required: true }]"
                  />
                  <van-field
                    v-model="formin.bankAccount"
                    name="银行账户"
                    label="银行账户"
                    placeholder="请输入银行账户"
                    :rules="[{ required: true }]"
                  />
                  <van-button class="next" round block type="info" native-type="submit">下一步</van-button>
                </van-form>
              </div>
              <div class="entryLast" v-else>
                <div class="entryList">
                  <van-radio-group v-model="radio"  direction="horizontal">
                    <van-radio name="1" @click="clickLocal('1')">同收货地址
                      <template #icon="props">
                        <img class="img-icon" :src="props.checked ? require('@/assets/image/yxz.png') : require('@/assets/image/wxz.png')" width="20px" height="20px"/>
                      </template>
                    </van-radio>
                    <van-radio name="2" @click="clickLocal('2')">上次邮寄地址
                      <template #icon="props">
                        <img class="img-icon" :src="props.checked ? require('@/assets/image/yxz.png') : require('@/assets/image/wxz.png')" width="20px" height="20px"/>
                      </template>
                    </van-radio>
                  </van-radio-group>
                </div>
                <van-form  @submit="submitOrder" ref='formin'>
                  <van-field
                      v-model="formin.recipient"
                      name="姓名"
                      label="姓名"
                      placeholder="请输入姓名"
                      :rules="[{ required: true }]"
                    />
                    <van-field
                      v-model="formin.recipientPhone"
                      name="手机号"
                      label="手机号"
                      placeholder="请输入手机号"
                      :rules="[{ required: true }]"
                    />
                    <van-field
                      readonly
                      clickable
                      v-model="formin.value"
                      name="所在地区"
                      label="所在地区"
                      placeholder="选择省-市-区-街道"
                      :rules="[{ required: true }]"
                      @click="showArea = true"
                    />
                    <van-popup v-model="showArea" position="bottom">
                      <van-area
                        :area-list="areaList"
                        @confirm="onConfirmInvoice"
                        @cancel="showArea = false"
                      />
                    </van-popup>
                    <van-field
                      v-model="formin.detailAddress"
                      name="详细地址"
                      label="详细地址"
                      placeholder="请输入详细地址"
                      :rules="[{ required: true }]"
                    />
                  <div class="last">
                    <div class="before" @click="backStep">上一步</div>
                    <van-button class="subform" round block type="info" native-type="submit">确定</van-button>
                  </div>
                </van-form>
              </div>
              <div class="bottomMsg">
                <img :src="require('@/assets/image/warn.png')" alt="" width="12px" height="12px"/>
                <p>
                  专票在订单签收后8-10个工作日开具并寄出，顺丰寄送，一般会在您订单完成后15个工作日内送达给您（如遇节假日稍有延迟）。请注意查收；增值税专用发票收到后请妥善保存，如退货请一同寄回，如退货专票未能寄回，则需扣除相应的税点。
                </p>
              </div>
            </div>
          </van-tab>
        </van-tabs>

        
        <!-- 发票选订单 -->
        <van-action-sheet v-model="showOrder" style="background: #f6f6f6; min-height: 600px;" title="未开发票订单">
          <van-sticky>
            <div class="topMost">
              <div class="topBody">
                <van-checkbox
                  v-model="checkAll"
                  shape="square"
                  checked-color="#f00"
                  icon-size="16px"
                  @click="selectClickAll"
                  ><span style="color: #666">选择开票订单</span></van-checkbox
                >
                <p>已选择订单金额 <span>{{radioCountAmount}}</span> 元</p>
              </div>
            </div>
          </van-sticky>
          <div class="orderMsge">
            <!-- 下面填写v-for -->
            <div class="onceWares" v-for="item in order.data" :key="item.id">
                <div class="topMsg">
                <p>订单号：{{item.orderSn}}</p>
                <p class="timer">{{item.createTime}}</p>
                </div>
                <div class="waresName">
                <van-checkbox v-model="item.visible" v-if="item" :disabled="Number(item.kpNum) === 0 || item.kpName === '退货中'" @click="orderClickDto(item, order.data)" shape="square" checked-color="#f00">
                  <div class="waresBody" v-for="itm in item.orderItemList" :key="itm.id">
                    <div class="wareImage">
                      <img :src="itm.productPic" alt="" />
                    </div>
                    <div class="wareTitle">
                      <div class="title">{{itm.productName}}</div>
                      <div class="deploy" v-if="itm.productAttr">
                        <p v-if="JSON.parse(itm.productAttr)[0]">{{JSON.parse(itm.productAttr)[0].value}}</p>
                        <p v-if="JSON.parse(itm.productAttr)[1]">{{JSON.parse(itm.productAttr)[1].value}}</p>
                      </div>
                      <div class="money">
                      </div>
                    </div>
                  </div>
                </van-checkbox>
                </div>
                <div class="allPrice">
                  <span v-if="item.kpName === '退货中'" class="font" style="font-size: 15px; margin-top: 3px; margin-right: 10px;">{{item.kpName}}</span><span>合计: <span class="font">￥{{item.kpNum}}</span></span>
                </div>
            </div>
            <div class="confirmBtn" @click="handleOrder(false)">确定</div>
          </div>
        </van-action-sheet>
      </van-action-sheet>
      <van-popup v-model="visibleInvoice" position="bottom" :style="{ height: '60%' }" closeable style="padding-top:50px">
        <div class="fapiaoTitle">已开发票信息</div>
        <van-cell-group v-if="formInvoice.type === 1">
          <van-cell title="发票抬头：" :value="formInvoice.invoiceTitle" />
          <van-cell title="邮箱：" :value="formInvoice.email" />
        </van-cell-group>
        <van-cell-group v-if="formInvoice.type === 2">
          <van-cell title="发票抬头：" :value="formInvoice.invoiceTitle" />
          <van-cell title="纳税人识别号：" :value="formInvoice.taxpayerId" />
          <van-cell title="邮箱：" :value="formInvoice.email" />
        </van-cell-group>
        <van-cell-group v-if="formInvoice.type === 3">
          <van-cell title="单位名称：" :value="formInvoice.invoiceTitle" />
          <van-cell title="纳税人识别号：" :value="formInvoice.taxpayerId" />
          <van-cell title="注册地址：" :value="formInvoice.companyAddress" />
          <van-cell title="注册电话：" :value="formInvoice.telphone" />
          <van-cell title="开户银行：" :value="formInvoice.bankName" />
          <van-cell title="银行账户：" :value="formInvoice.bankAccount" />
          <van-cell title="姓名：" :value="formInvoice.recipient" />
          <van-cell title="手机号码：" :value="formInvoice.recipientPhone" />
          <van-cell title="所在详细地址："/>
          <van-cell value-class="fontColor" :value="formInvoice.province + '--' + formInvoice.province + '--' + formInvoice.city + '--' + formInvoice.region + '--' + formInvoice.town + '--' + formInvoice.detailAddress" />
        </van-cell-group>
      </van-popup>
      <div style="height: 60px"></div>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "@/data/api/market/index"
import Header from '@/components/Layout/header/index'
import { areaList } from '@vant/area-data'
export default {
  components :{
    Header
  },
  data() {
    return {
      visibleInvoice:false,
      formInvoice: {},
      // forOnce:0,
      confirm: false,
      isCancel: false,
      flagsss: '',
      checked: false,
      dataList: [],
      priceData: {
