  filters:{
    automaticReceipt(time, days) {
      let overTime = new Date(time).getTime() + (days * (24 * 60 * 60 * 1000))
      let countDown = overTime - new Date().getTime()
      let returnTime = `${parseInt(countDown / (24 * 60 * 60 * 1000))}天${parseInt((countDown % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60))}时`
      return returnTime
    },
    isInvoice(arr) {
      let btn = null
      arr.forEach(item => {
        if(item.type === 3) {
          btn = false
        }
      })
      return btn
    },
    statusFilter (itm) {
        // 0->待付款；1->待发货；2->已发货；3->已完成；4->已关闭；5->无效订单
        if (itm.status === 0) {
          if (itm.payType === 3 && itm.payVoucher) {
            return '待审核'
          } else {
            return '待付款'
          }
        } else if (itm.status === 1) {
          return '待发货'
        } else if (itm.status === 2) {
          return '待收货'
        } else if (itm.status === 3) {
          return '已完成'
        } else if (itm.status === 4) {
          return '已取消'
        } else if (itm.status === 5) {
          return '无效订单'
        }
      },
      fontJudge(itm){
        if (itm.status === 0) {
          if (itm.payType === 3 && itm.payVoucher) {
            return '待审核'
          } else {
            return '等待买家付款'
          }
        } else if (itm.status === 1) {
          return '等待卖家发货'
        } else if (itm.status === 2) {
          return '卖家已发货'
        } else if (itm.status === 3) {
          return '订单已完成'
        } else if (itm.status === 4) {
          return '订单已取消'
        }
      }
  },
  mounted(){
    let me = this
    this.flagsss = setInterval(function() {
      me.shopdetails();
    }, 100);
    
    if (window.history && window.history.pushState) {
      // 向历史记录中插入了当前页
      history.pushState(null, null, document.URL);
      window.addEventListener('popstate', this.goBack, false);
    }
  },
  destroyed () {
    window.removeEventListener('popstate', this.goBack, false);
    history.go(0)
  },
  methods: {
    lookOver(id,fid,obj,ffid) {
      obj.forEach((term) => {
        if((obj.status !== 3 && (term.orderItemId && term.orderItemId === ffid)) || (!term.orderItemId && obj.status !== 3)) {
          if(term.type === 1){
            this.$router.push({
              path: "/user/returnGoods",
              query:{
                  orderSn:id,
                  id: fid,
                  type: term.type,
                  ffid: ffid
              }
            })
          } else if (term.type === 2) {
            this.$router.push({
              path: "/user/returnRefund",
              query:{
                  orderSn:id,
                  id: fid,
                  type: term.type,
                  ffid: ffid
              }
            })
          } else if (term.type === 3) {
            this.$router.push({
              path: "/user/returnExchange",
              query:{
                orderSn:id,
                id: ffid,
              }
            })
          }
        }
      })
    },
    aa(id, arr, fid){
      if(arr.length === 0){
        return false
      }
      let newId = null
      let newArr = []
      arr.forEach(i=>{
        if (i.status !== 3) {
          if (i.orderItemId) {
            if(id === i.orderItemId){
              newArr.push(i.orderItemId)
              newId = id
            }
          } else {
            if(fid === i.orderId){
              newArr.push(i.orderId)
              newId = fid
            }
          }
        }
      })
      if (newId && newId === newArr[0]) {
        return true
      } else {
        return false
      }
    },
    seeInvoice(invoiceId){
      let me = this
      api.getInvoiceById(invoiceId).then(res => {
        if (res.code === 200) {
          me.formInvoice = res.data
          me.visibleInvoice = true
        } else {
          me.$message.error(res.message);
          if (res.code === 401) {
            me.$router.push({
              path:'/web/home',
            })
          }
        }
      }).catch((res) => {
        me.$message.error(res.message);
      }).finally(() => {
      })
    },
    goBack () {
      // sessionStorage.clear();
      // window.history.back();
      if (localStorage.getItem('historyUrl') === '/user/orderdetails') {
        this.$router.push({
          path:'/market/cart'
        })
      } else {
        this.$router.push({
          path:'/user/myorders',
          query: {
            'toorders': -1
          }
        })
      }
    },
    orderClickDto (item, arr) {
      let me = this
      if (item.visible) {
        me.radioCount += 1
        me.radioCountAmount = me.accAdd(Number(me.radioCountAmount), item.kpNum).toFixed(2)
      } else {
        me.radioCount -=1
        me.radioCountAmount = me.accSub(Number(me.radioCountAmount), item.kpNum).toFixed(2)
      }
      if(me.radioCount === arr.length) {
        me.checkAll = true
      } else {
        me.checkAll = false
      }
      // this.handleOrder(true)
    },
    accSub (arg1, arg2) {
      let r1, r2, m, n;
      try {
        r1 = arg1.toString().split('.')[1].length;
      } catch (e) {
        r1 = 0;
      }
      try {
        r2 = arg2.toString().split('.')[1].length;
      } catch (e) {
        r2 = 0;
      }
      m = Math.pow(10, Math.max(r1, r2));
      n = r1 >= r2 ? r1 : r2;
      return Number(((arg1 * m - arg2 * m) / m).toFixed(4));
    },
    accAdd (arg1, arg2) {
      let r1, r2, m;
      try {
        r1 = arg1.toString().split('.')[1].length;
      } catch (e) {
        r1 = 0;
      }
      try {
        r2 = arg2.toString().split('.')[1].length;
      } catch (e) {
        r2 = 0;
      }
      m = Math.pow(10, Math.max(r1, r2));
      return (arg1 * m + arg2 * m) / m;
    },
    onConfirmInvoice (values) {
      this.formin.value = values
        .filter((item) => !!item)
        .map((item) => item.name)
        .join('/');
      let em = this
      values.map(item => {
        em.formin.province = values[0].name
        em.formin.provinceId = values[0].code.substring(0,2)
        em.formin.city = (values[1].name === '天津市' || values[1].name === '北京市' || values[1].name === '上海市') ? '市辖区' : values[1].name
        em.formin.cityId = values[1].code.substring(0,4)
        em.formin.region = values[2].name
        em.formin.regionId = values[2].code
      })
      this.showArea = false;
    },
    submitOrder () {
      let me = this
      // if(me.forOnce === 1){
      //   me.forOnce = 2
      // }else{
      //   me.forOnce = 1
      // }
      me.formin.invoiceAmount = me.order.amount
