            } else {
              me.$message.error(res.message);
            }
          }).catch((res) => {
            me.$message.error(res.message);
          }).finally(() => {
          })
        // }else{
        //   let newOrderInvoice = params.orderInvoice
        //   newOrderInvoice.id = me.invoiceId
        //   newOrderInvoice.corporation = {
        //     cityId: `${newOrderInvoice.cityId}/${newOrderInvoice.city}`,
        //     provinceId: `${newOrderInvoice.provinceId}/${newOrderInvoice.province}`,
        //     regionId: `${newOrderInvoice.regionId}/${newOrderInvoice.region}`,
        //     townId: `${newOrderInvoice.townId}/${newOrderInvoice.town}`
        //   }
        //   api.invoiceUpdate(newOrderInvoice).then(res => {
        //     if (res.code === 200) {
        //       me.$toast.success('修改成功');
        //       me.shopdetails();
        //     } else {
