        //     }
        //   }).catch((res) => {
        //     me.$message.error(res.message);
        //   }).finally(() => {
        //   })
        // }
      } else {
        me.$notify({
          message: "专用发票：开票金额不能小于10000",
          color: '#ad0000',
          background: '#ffe1e1',
        })
      }
    },
    closeOrder () {
      if (me.formin.orderIds.length > 0) {
        me.order.data.map(item => {
          if (!me.formin.orderIds.includes(item.id)) {
            item.visible = false
          }
          return item
        })
      } else {
        me.order.data.map(item => {
          item.visible = false
          return item
        })
      }
      me.showOrder = false
    },
    handleOrder (flag) {
      let me = this
      me.formin.orderIds = []
      me.order.amount = me.checked ? me.payMoney: me.priceData.payAmount
      me.radioCountAmount = me.checked ? me.payMoney: me.priceData.payAmount
      me.order.data = me.order.data.map(item => {
        if (item.visible) {
          me.formin.orderIds.push(item.id)
          me.order.amount = me.accAdd(me.order.amount, item.kpNum).toFixed(2)
          me.radioCountAmount = me.accAdd(me.radioCountAmount, item.kpNum).toFixed(2)
        }
        return item
      })
      me.showOrder = flag || false
      me.showInvoice = true
    },
    selectClickAll () {
      let me = this
      if (me.checkAll) {
        me.radioCount = me.order.data.length
        me.order.data = me.order.data.map(item => {
          if (!item.visible && Number(item.kpNum) !== 0 && item.kpName !== '退货中') {
            // me.order.amount = me.accAdd(me.order.amount, item.kpNum).toFixed(2)
            me.radioCountAmount = me.accAdd(me.radioCountAmount, item.kpNum).toFixed(2)
            item.visible = true
          }
          return item
        })
      } else {
        me.radioCount = 0
        me.order.data = me.order.data.map(item => {
          if (item.visible) {
            item.visible = false
            // me.order.amount = me.accSub(me.order.amount, item.kpNum).toFixed(2)
            me.radioCountAmount = me.accSub(me.radioCountAmount, item.kpNum).toFixed(2)
          }
          return item
        })
        // me.order.amount = me.checked ? me.payMoney: me.priceData.payAmount
      }
    },
    nextStep () {
      this.invChoice = false
      this.clickLocal('1')
    },
    backStep () {
      this.invChoice = true;
    },
    clickRadio (evt) {
      if (evt === '2') {
        this.formfm.invoiceTitle = '';
        this.formfm.email = ''
      } else {
        this.formfm.invoiceTitle = '个人';
        this.formfm.email = ''
      }
    },
    clickLocal (evt) {
      let me = this
      if (evt === '1') {
        if (me.addRessData) {
          me.formin.recipient = me.addRessData.name
          me.formin.recipientPhone = me.addRessData.phoneNumber
          me.formin.value = me.addRessData.province + '/' + (me.addRessData.city === '市辖区' ? me.addRessData.province : me.addRessData.city) + '/' + me.addRessData.region
          me.formin.province = me.addRessData.province
          me.formin.provinceId = me.addRessData.provinceId
          me.formin.city = me.addRessData.city
          me.formin.cityId = me.addRessData.cityId
          me.formin.region = me.addRessData.region
          me.formin.regionId = me.addRessData.regionId
          me.formin.town = me.addRessData.town || ''
          me.formin.townId = me.addRessData.townId || ''
          me.formin.detailAddress = me.addRessData.detailAddress
        }
      } else {
        api.getLastInvoice({'invoiceType': 3}).then(res => {
          if (res.code === 200) {
            if (res.data) {
              me.formin.recipient = res.data.recipient
              me.formin.recipientPhone = res.data.recipientPhone
              me.formin.value = res.data.province + '/' + (res.data.city === '市辖区' ? res.data.province : res.data.city) + '/' + res.data.region
              me.formin.province = res.data.province
              me.formin.provinceId =res.data.provinceId
              me.formin.city = res.data.city
              me.formin.cityId = res.data.cityId
              me.formin.region = res.data.region
              me.formin.regionId = res.data.regionId
              me.formin.town = res.data.town || ''
              me.formin.townId = res.data.townId || ''
              me.formin.detailAddress = res.data.detailAddress
            } else {
              me.$notify({
                message: '暂无历史发票信息',
                color: '#ad0000',
                background: '#ffe1e1',
              })
            }
          } else {
            me.$notify({
              message: res.message,
              color: '#ad0000',
              background: '#ffe1e1',
            })
          }
        }).catch((res) => {
          me.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }).finally(() => {
        })
      }
    },
    onCancelInvoice () {
      this.formin.orderIds = []
    },
    toOrderList () {
      let me = this
      if (me.order.data.length < 1) {
        api.getOrderList(me.order.params).then(res => {
          if (res.code === 200) {
            if (res.data.list) {
              if (typeof(me.formin.orderIds) === String) {
                me.formin.orderIds = me.formin.orderIds.split(',')
              }
              res.data.list.forEach((item, index, arr) => {
                if(item.id === Number(me.$route.query.id)){
                  arr.splice(index, 1)
                }
              })
              res.data.list = res.data.list.map((item, index, arr) => {
                let flag = true
                item.kpNum = item.freightAmount
                me.formin.orderIds && me.formin.orderIds.filter(od => {
                  if (od === item.id) {
                    item.visible = true
                    flag = false
                  }
                })
                if (flag) {
                  item.visible = false
                }
                item.orderItemList.map((term, termIndex) => {
                  item.kpNum = (Number(item.kpNum) + Number(term.realAmount)).toFixed(2)
                  item.returnApplyList.forEach((itm) => {
                    if(term.id === itm.orderItemId && itm.type === 1 || itm.type === 2) {
                      item.payAmount -= term.productPrice
                    }
                  })
                })
                item.kpName = '-'
                item.returnApplyList.forEach((itm) => {
                  if (itm.status !== 3) {
                    item.kpNum = (Number(item.kpNum) - Number(itm.returnAmount)).toFixed(2)
                  }
                  if (itm.type === 1 && itm.status === 1) {
                    item.kpName = '退货中'
                  }
                })

                return item
              })
              me.order.data = res.data.list
            }
          } else {
            me.$notify({
              message: res.message,
              color: '#ad0000',
              background: '#ffe1e1',
            })
          }
        }).catch((res) => {
          me.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }).finally(() => {
          me.showOrder = true
        })
      } else {
        let ldt = 0
        let dis = 0
        me.order.data = me.order.data.map(item => {
          item.visible = false
          me.formin.orderIds && me.formin.orderIds.filter(od => {
            if (od === item.id) {
              item.visible = true
              ldt++
            }
          })
          me.showOrder = true
          if (Number(item.kpNum) === 0 || item.kpName === '退货中') {
            dis++
          }
          return item
        })
        if (ldt === 0) {
          me.checkAll = false
        } else {
          if (ldt === (me.order.data.length - dis)) {
            me.checkAll = true
          } else {
            me.checkAll = false
          }
        }
        me.radioCountAmount = me.order.amount
      }
    },
