    shopdetails() {
      clearInterval(this.flagsss)
      let me = this;
      api.getOrderdetail({'id': me.$route.query.id}).then((res) => {
          if (res.code === 200) {
            if (res.data) {
                  me.dataList = [res.data];
                  me.orderId = res.data.id
                  res.data.invoiceId ? this.invoiceId = res.data.invoiceId : this.invoiceId = ''
                  me.priceData.freightAmount = res.data.freightAmount
                  me.priceData.totalAmount = res.data.totalAmount
                  me.priceData.promotionAmount = res.data.promotionAmount
                  me.priceData.payAmount = res.data.payAmount
                  me.addRessData = {
                    'name':res.data.receiverName,
                    'phoneNumber':res.data.receiverPhone,
                    'province':res.data.receiverProvince,
                    'provinceId':res.data.receiverProvinceId,
                    'city':res.data.receiverCity,
                    'cityId':res.data.receiverCityId,
                    'region':res.data.receiverRegion,
                    'regionId':res.data.receiverRegionId,
                    'town':res.data.receiverTown,
                    'townId':res.data.receiverTownId,
                    'detailAddress':res.data.receiverDetailAddress,
                  }
                  res.data.payVoucherList.forEach(item => { 
                      if(item.confirm === 0 || item.confirm === 1){
                          me.isCancel = true
                          }
                      });
                  
                  res.data.payVoucherList.forEach(item => {
                    if(item.confirm === 0){
                      me.confirm = true
                    }
                  });
                  res.data.returnApplyList.forEach((item,index) => {
                    if(item.type === 1 && item.status === 3 || item.type === 2 && item.status === 3 || item.type === 3 && item.status === 3){
                      res.data.returnApplyList.splice(index, 1)
                    }
                  });
                  if(res.data.returnApply){
                    me.returnApply = res.data.returnApply
                  }
            } else {
              me.dataList = [];
            }
          } else {
            me.$notify({
              message: res.message,
              color: '#ad0000',
              background: '#ffe1e1',
            })
          }
        }).catch((res) => {
          me.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }).finally(() => {});
    },
