    nowPayment(item){
      localStorage.setItem('historyUrl', '')
      let me = this
        if (item) {
          if (item.id) {
            me.dataList.filter(itm => {
              if (itm.orderSn === item.orderSn) {
                me.product = itm
              }
            })
          }
          me.$router.push({
            path:'/user/payment',
            query: {
              order: JSON.stringify(item),
              orderItemList : JSON.stringify(me.product.orderItemList[0])
            }
          })
        } else {
          me.$router.push({
            path:'/user/payment',
            query: {
              order: JSON.stringify(me.product),
              orderItemList : JSON.stringify(me.product.orderItemList[0])
            }
          })
        }
    },
    toLogistics(muster) {
      this.$router.push({
        path: "/user/logistics",
        query:{
          sw: JSON.stringify(muster)
        }
      });
    },
    toInvoice(row) {
      let me = this
      let num = 0
      row.returnApplyList.map(item =>{
        if ((item.type === 1 || item.type === 2) && item.status === 2) {
          num = num + Number(item.returnAmount)
        }
      })
      me.showInvoice = true
      me.order.amount = (row.useCredit && row.useCredit > 0) ? row.useCredit : (Number(row.payAmount)-num).toFixed(2)
      me.radioCountAmount = (row.useCredit && row.useCredit > 0) ? row.useCredit : (Number(row.payAmount)-num).toFixed(2)
      me.formfm.invoiceAmount = (row.useCredit && row.useCredit > 0) ? row.useCredit : (Number(row.payAmount)-num).toFixed(2)
      me.formin.invoiceAmount = (row.useCredit && row.useCredit > 0) ? row.useCredit : (Number(row.payAmount)-num).toFixed(2)
      me.order.data = []
    },
    getInvoiceFlag(product){
      let flag = true
      // product.returnApplyList.map(item =>{
      //   if (item.type === 1 || item.type === 3) {
      //     if (item.status === 2 || item.status === 3) {
      //       flag = true
      //     } else {
      //       flag = false
      //     }
      //   }
      // })
      // if (!flag && (product.orderItemList.length > product.returnApplyList.length)) {
      //   flag = true
      // }
      product.returnApplyList.map(itemOn =>{
        if (itemOn.type === 1 && (itemOn.status === 0 || itemOn.status === 1)) {
          flag = false
        }
      })
      return flag
    },
    sureRece(id) {
      this.$dialog
        .confirm({
          title: "温馨提示",
          message: "您确定要收货吗？",
        }).then(() => {
          let me = this
          api.orderConfirmReceiveOrder({id: id}).then(res => {
            if (res.code === 200) {
              me.$toast.success('收货成功');
              this.$router.push({
              path:'/user/myorders',
              query: {
                'toorders':'-1',
              }
            })
            } else {
              me.$notify({
                message: res.message,
                color: '#ad0000',
                background: '#ffe1e1',
              })
            }
          }).catch((res) => {
            me.$notify({
              message: res.message,
              color: '#ad0000',
              background: '#ffe1e1',
            })
          }).finally(() => {
          })
        }).catch(() => {
        });
    },
    applyReturn(id,type,Fid,allow,arr,status){
      let otherType = null
      if(allow){
        if(allow === 1){
          if(status === 1) {
            otherType = 2
            this.$router.push({
              path: "/user/applyReturn",
              query:{id:id,type: 2, otherType: otherType, Fid:Fid}
            })
            return
          }
          this.$router.push({
            path: "/user/applyReturn",
            query:{id:id,type: type,Fid:Fid,length: arr.length}
          });
        }else{
          this.$notify({
            message: '该商品不允许退货',
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }
      }else{
        this.$router.push({
          path: "/user/applyReturn",
          query:{id:id,type: status === 1 ? 2 : 1, Fid:Fid}
        });
      }
    },
    delOrder(id){
      this.$dialog
        .confirm({
          title: "温馨提示",
          message: "您确定要删除该订单吗？",
        })
        .then(() => {
          let me = this
        api.deleteOrder({id: id}).then(res => {
          if (res.code === 200) {
          me.$toast.success('删除成功');
          this.$router.push({
              path:'/user/myorders',
              query: {
                'toorders':'-1',
              }
            })
          } else {
            me.$notify({
              message: res.message,
              color: '#ad0000',
              background: '#ffe1e1',
            })
          }
        }).catch((res) => {
          me.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }).finally(() => {
        })
        })
        .catch(() => {
        });
    }
  },
};
</script>

